.projects-section {
  padding: 80px 20px;
  background: #fff; /* match About section bg */
  color: #000;
}

.projects-title {
  font-size: 2.5rem;
  margin-bottom: 50px;
  font-weight: 700;
  text-align: center;
  color: #FF7F50;
}

/* GRID */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 30px;
  max-width: 1200px;
  margin: auto;
}

/* CARD */
.project-card {
  background: #5C3E41;
  border-radius: 14px;
  overflow: hidden;
  transition: all 0.3s ease;
  border: 1px solid #1e293b;
}

.project-card:hover {
  transform: translateY(-6px);
  border-color: #FF7F50;
}

/* IMAGE */
.project-image-container {
  height: 210px;
  overflow: hidden;
}

.project-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.4s ease;
}

.project-card:hover .project-image {
  transform: scale(1.05);
}

/* CONTENT */
.project-content {
  padding: 20px;
}

.project-content h3 {
  font-size: 1.3rem;
  color: #f8fafc;
  margin-bottom: 8px;
}

.project-content p {
  font-size: 0.95rem;
  color: #fff;
  line-height: 1.5;
  margin-bottom: 14px;
}

/* TECH */
.tech-badges {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 16px;
}

.badge {
  padding: 5px 12px;
  font-size: 0.75rem;
  border-radius: 20px;
  background: #1e293b;
  color: #FF7F50;
}

/* BUTTON */
.btn-project {
  display: inline-block;
  padding: 10px 18px;
  background: #FF7F50;
  color: #020617;
  border-radius: 8px;
  font-weight: 600;
  text-decoration: none;
  transition: background 0.3s ease;
}

.btn-project:hover {
  background: #0ea5e9;
}
.projects-slider .project-card {
  margin: 0 10px;
}

.slick-slide {
  padding: 0 10px;
}