.skills-section {
  background: #5c3e41;
  color: #f8fafc;
  padding: 80px 20px;
}

.skills-inner {
  max-width: 1100px;
  margin: 0 auto;
  display: flex;
  gap: 40px;
  align-items: center;
  justify-content: space-between;
}

/* LEFT IMAGE */
.skills-left {
  flex: 1 1 380px;
  display: flex;
  justify-content: center;
}

.skills-illustration {
  width: 100%;
  max-width: 380px;
  border-radius: 14px;
  border: 1px solid #1e293b;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.4);
}

/* RIGHT CONTENT */
.skills-right {
  flex: 1 1 520px;
  min-width: 280px;
}

.skills-title {
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 6px;
  color: #f8fafc;
}

.skills-subtitle {
  font-size: 0.95rem;
  color: #94a3b8;
  margin-bottom: 28px;
}

/* SKILLS LIST */
.skills-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* SKILL ROW */
.skill-row {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

/* META */
.skill-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.skill-name {
  font-size: 0.95rem;
  font-weight: 600;
  color: #f8fafc;
}

.skill-value {
  font-size: 0.85rem;
  color: #94a3b8;
}

/* BAR */
.skill-bar {
  height: 10px;
  background: #020617;
  border-radius: 6px;
  overflow: hidden;
  border: 1px solid #1e293b;
}

/* FILL */
.skill-bar-fill {
  height: 100%;
  background: #ff7f50;
  border-radius: 6px;
  transition: width 1s ease-in-out;
}

/* RESPONSIVE */
@media (max-width: 880px) {
  .skills-inner {
    gap: 28px;
    flex-direction: column;
    align-items: center;
  }

  .skills-left,
  .skills-right {
    order: unset;
    width: 100%;
  }

  .skills-title {
    font-size: 1.9rem;
    text-align: center;
  }

  .skills-subtitle {
    text-align: center;
  }
}