.about-section {
  padding: 100px 20px;
  background: #5C3E41;
  color: #fff;
}

.about-container {
  max-width: 1200px;
  margin: auto;
  display: grid;
  grid-template-columns: 1.2fr 1fr;
  gap: 60px;
  align-items: center;
}

/* LEFT CONTENT */
.about-tag {
  color: #ff7f50;
  letter-spacing: 2px;
  font-size: 0.9rem;
  font-weight: 600;
}

.about-content h2 {
  font-size: 2.8rem;
  margin: 15px 0 20px;
  line-height: 1.2;
}

.about-content h2 span {
  color: #ff7f50;
}

.about-content p {
  color: #fff;
  line-height: 1.7;
  margin-bottom: 15px;
  font-size: 1rem;
}

/* STATS */
.about-stats {
  display: flex;
  gap: 40px;
  margin-top: 30px;
}

.about-stats div h3 {
  font-size: 2rem;
  color: #ff7f50;
}

.about-stats div span {
  font-size: 0.9rem;
  color: #fff;
}

/* RIGHT IMAGE */
.about-image-box {
  position: relative;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 30px 60px rgba(0, 0, 0, 0.6);
}

.about-image-box img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px;
  transition: transform 0.5s ease;
}

.about-image-box:hover img {
  transform: scale(1.05);
}

/* RESPONSIVE */
@media (max-width: 900px) {
  .about-container {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .about-stats {
    justify-content: center;
    flex-wrap: wrap;
  }
}